<template>
  <div class="content_off">
    <ul>
      <van-list v-model="loading" :finished="finished" finished-text="我是有底线的..." @load="onLoad"
                :immediate-check="true">
        <li v-for="value in films" @click="detail(value.filmId)">
          <div class="movie_img">
            <img :src="value.poster" class="fimg">
          </div>
          <div class="right">
            <p class="title">{{ value.name }}<span class="timeType">{{ value.item.name }}</span></p>
            <p class="actors">主演：{{ value.actors | otoa }}</p>
            <p>上映日期：{{ date(value.premiereAt) }}</p>
          </div>
          <button type="button" class="gbtn">预购</button>
        </li>
      </van-list>

    </ul>
  </div>
</template>

<script>
  import axios from "axios"
  import vue from "vue"

  vue.filter("otoa", value => {
    return value.reduce((prev, value) => {
      return prev + value.name + " "
    }, "")
  })
  export default {
    data() {
      return {
        films: [],
        page: 1,
        timer: '',
        finished: false,
        loading: false,
      }
    },
    mounted() {
      // window.addEventListener('scroll', this.scrollto)
      // this.axios_(this.page)
    },
    methods: {
      axios_(page) {
        axios({
          url: `https://m.maizuo.com/gateway?cityId=${this.$store.state.cityId}&pageNum=${page}&pageSize=10&type=2&k=1894032`,
          headers: {
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16339322892594864621420545","bc":"110100"}',
            'X-Host': 'mall.film-ticket.film.list'
          },
        })
          .then(value => {
            this.films.push(...value.data.data.films);
            this.page++;
            this.loading = false;
            if (value.data.data.films.length === 0) {
              this.finished = true
            }
            if ((this.films.length === value.data.data.total - 1) && value.data.data.total > 10) {
              this.finished = true
            }
            if (value.data.data.total <= 10 && (this.films.length === value.data.data.total)) {
              this.finished = true
            }
          })
      },
      onLoad() {
        this.axios_(this.page);
      },
      // scrollto() {
      // 	clearTimeout(this.timer)
      // 	let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // 	if (document.documentElement.scrollHeight - scrollTop - window.innerHeight < 50) {
      // 		this.timer = setTimeout(() => {
      // 			this.axios_(this.page)
      // 			this.lock = false
      // 		}, 300)
      // 	} else {
      // 		clearTimeout(this.timer)
      // 	}
      // },
      date(date) {
        let c = new Date(date * 1000);
        let n = new Date().getFullYear() === c.getFullYear() ? "" : c.getFullYear() + "年"
        return this.week(c.getDay()) + " " + n + (c.getMonth() + 1) + "月" + c.getDate() + "日"
      },
      week(number) {
        let n = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
        return n[number];
      },
      detail(id) {
        this.$router.push(`/main/movie/on/detail/${id}`)
      }
    }
  }
</script>

<style scoped lang="scss">
  .content_off {
    margin-bottom: 3.4375rem;
    background: #fff;

    .movie_img {
      width: 4.125rem;
      height: 6.095625rem;
    }

    li {
      padding: 15px 15px 15px 0;
      box-sizing: border-box;
      border-bottom: 0.0625rem solid #ededed;
      margin-left: 15px;
      list-style: none;
      display: flex;
      height: auto;

      .fimg {
        width: 4.125rem;
      }

      .gbtn {
        border: 1px solid #ffb232;
        width: 50px;
        height: 1.5625rem;
        flex: 0 0 50px;
        border-radius: 4px;
        background-color: inherit;
        color: #ffb232;
        align-self: center;
      }

      .right {
        overflow: hidden;
        flex: 1;
        padding: 0.9375rem 10px;


        .title {
          font-size: 1rem;
          color: #191a1b;
          display: flex;
          align-items: center;
          line-height: 1.375rem;

          .timeType {
            font-size: 9px;
            color: #fff;
            background-color: #d2d6dc;
            height: 0.875rem;
            line-height: 0.875rem;
            padding: 0 0.125rem;
            border-radius: 0.125rem;
            display: inline-block;
            margin-left: 5px;
          }
        }

        p {
          flex: 0;
          font-size: 0.8125rem;
          color: #797d82;
          line-height: 1.25rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }

        .grade {
          color: #ffb232;
          font-size: 0.875rem;
          margin-left: 0.25rem;
          flex: 0;
        }
      }
    }
  }
</style>
